<template>
  <v-toolbar id="core-toolbar" flat prominent color="#189ACA" scroll-off-screen>
    <div class="v-toolbar-title">
      <v-toolbar-title class="white--text font-weight-bold">
        <v-breadcrumbs
          light
          :items="items"
          class="ftc_or"
          divider=">"
        ></v-breadcrumbs>
      </v-toolbar-title>
    </div>
    <v-spacer />
    <v-toolbar-items>
      <v-layout align-center justify-end>
        <img
          src="../../assets/images/admin.png"
          id="view-fullscreen"
          style=" width: 20px; height:20px; margin-right:10px "
        />
        <span style="margin-right:30px;color:white">{{ user }}</span>
        <button @click="tab()" class="btn">
          <img
            src="../../assets/images/全屏.png"
            id="view-fullscreen"
            style=" width: 20px; height:20px; margin-right:30px"
          />
        </button>
        <button @click="exit()" style="color:white" class="btn">
          <img
            src="../../assets/images/exit.png"
            style=" width: 20px; height:20px;"
          />
        </button>
        <!-- <v-flex class="bg_deepb">
          <span class="white--text subheading font-weight-medium">
            {{
            time
            }}
          </span>
        </v-flex>-->
        <v-flex>
          <v-avatar></v-avatar>
        </v-flex>
      </v-layout>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
import dayjs from '@/services/dayjs'

export default {
  data() {
    return {
      user: '',
      a: 1,
      title: '多用户远程控制系统',
      responsive: false,
      responsiveInput: false,
      time: '',
      timer: null,
      items: [
        {
          text: '多用户远程控制系统',
          disabled: false,
          href: '/'
        }
      ]
    }
  },

  created() {
    this.user = localStorage.getItem('admin')
    console.log('user' + this.user)
    this.time = dayjs().format('YYYY年MM月DD日 HH:mm dddd')
    this.timer = window.setInterval(() => {
      this.time = dayjs().format('YYYY年MM月DD日 HH:mm dddd')
    }, 60 * 1000)
  },

  mounted() {
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  beforeDestroy() {
    window.clearInterval(this.timer)
    window.removeEventListener('resize', this.onResponsiveInverted)
  },
  methods: {
    tab() {
      if (this.a == 1) {
        this.fullScreen()
        this.a = 2
      } else {
        this.a = 1
        this.exitScreen()
      }
    },
    exit() {
      this.$axios({
        method: 'get',
        url: 'logout/'
      }).then((res) => {
        console.log(res)
        if (res.data.code === 200) {
          localStorage.clear()
          this.$router.push({ name: 'Login' })
          this.$message.success('退出成功')
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },

    fullScreen() {
      this.a = 1
      var el = document.documentElement
      var rfs =
        el.requestFullScreen ||
        el.webkitRequestFullScreen ||
        el.mozRequestFullScreen ||
        el.msRequestFullscreen
      if (typeof rfs != 'undefined' && rfs) {
        rfs.call(el)
      }
      return
    },
    //退出全屏
    exitScreen() {
      this.a = 2
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen()
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen()
      }

      this.a = 1
    },

    onClickBtn() {
      this.setDrawer(!this.$store.state.app.drawer)
    },

    onResponsiveInverted() {
      if (window.innerWidth < 991) {
        this.responsive = true
        this.responsiveInput = false
      } else {
        this.responsive = false
        this.responsiveInput = true
      }
    }
  }
}
</script>
<style scoped>
#core-toolbar a {
  text-decoration: none;
}
#core-toolbar {
  background-color: #f1870d !important;
}
.btn {
  outline: none;
}
</style>
<style>
.v-toolbar .v-breadcrumbs__item {
  font-size: 30px !important;
  color: #011e48 !important;
}
</style>
